$(".auth-modal__toggle").click(function () {
    $(".auth-modal__mode").fadeIn()
    $(this).parents(".auth-modal__mode").hide(10)
})

$(".header__user-btn,.header__cart-btn").click(function () {
    $(this).parents(".header__btn").addClass("_active")
})

$(document).mouseup(function(e) {
    var div = $('.m-dropdown');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).parents(".header__btn").hasClass("_active")){
            $(div).parents(".header__btn").removeClass("_active")
        }

        if($(div).parents(".brand-filter").hasClass("_active")){
            $(div).parents(".brand-filter").removeClass("_active")
        }
    }
});

$(".burger").click(function () {
    $(".header").toggleClass("_open")
    $("body").toggleClass("fixed-body")
})
