$(".brand-filter__btn,.brand-filter__btn2").click(function () {
    $(".brand-filter").addClass("_active")
    if(window.innerWidth<=768){
        $("body").addClass("fixed-body")
    }
})

$(".all-brands__bg,.all-brands__close").click(function () {
    $(".brand-filter").removeClass("_active")
    $("body").removeClass("fixed-body")
})

let ranges = document.querySelectorAll(".props-filter__range")

$(document).ready(function() {

    ranges.forEach(el => {

        let rangeSlider = el.querySelector('.range-slider');
        let rangeFrom = el.querySelector('.props-filter__range-from');
        let rangeTo = el.querySelector('.props-filter__range-to');





        if (rangeSlider) {
            noUiSlider.create(rangeSlider, {
                start: [+rangeSlider.dataset.min, +rangeSlider.dataset.max],
                connect: true,
                step: 1,
                range: {
                    'min': +rangeSlider.dataset.min,
                    'max': +rangeSlider.dataset.max
                }
            });
        }



        let rangeOutputTo = el.querySelector('.noUi-handle-upper');
        let rangeOutputFrom = el.querySelector('.noUi-handle-lower');

        if (rangeSlider) {
            rangeSlider.noUiSlider.on('update', function(values, handle) {
                setRangeIntegerOutput(rangeOutputTo)
                setRangeIntegerOutput(rangeOutputFrom)
            })
        }
        setRangeIntegerOutput(rangeOutputTo)
        setRangeIntegerOutput(rangeOutputFrom)
    })

})


function setRangeIntegerOutput(el) {
    let rangeOutputText = el.getAttribute("aria-valuetext")
    el.setAttribute("data-output",parseInt(rangeOutputText))
}


$(".country-select__item").click(function () {
    let toggle = $(this).parents(".country-select").find(".country-select__toggle")
    let flagImgSrc = $(this).find(".country-select__toggle-flag").attr("src")
    let countryName = $(this).find(".country-select__toggle-title").text()

    $(this).parents(".country-select").find(".country-select__item").removeClass("_selected")
    $(this).addClass("_selected")

    $(toggle).find(".country-select__toggle-flag").attr("src",flagImgSrc)
    $(toggle).find(".country-select__toggle-title").html(countryName)

    $(this).parents(".country-select").removeClass("_active")
})

$(".country-select__toggle").click(function () {
    $(this).parents(".country-select").toggleClass("_active")
})

$(document).mouseup(function(e) {
    var div = $('.country-select');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).hasClass("_active")){
            $(div).removeClass("_active")
        }
    }
});

$(".props-filter__toggle").click(function () {
    $(this).addClass("_active")
})

$(".props-filter__bg,.props-filter__close").click(function () {
    $(".props-filter__toggle").removeClass("_active")
})
